import React, { useState, useEffect } from "react";
import { Card, CardHeader } from "@material-ui/core/";
import {
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableRow,
  CardContent,
  Typography,
  Paper,
  IconButton,
  Icon,
  Grid,
  TextField,
  Button,
} from "@material-ui/core";
import Box from "@material-ui/core/Box";
import { InputLabel } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import MinimizeIcon from "@material-ui/icons/Minimize";
import { postHttp } from "../../lib/common/HttpService";
import * as endpointContants from "../../utils/ptmsEndpoints";
import Toast from "../../lib/components/toast/ErrorToast";
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import moment from "moment";
import AppDatePicker from "../../lib/components/datepicker/appDatePicker";
import { FormProvider, useForm } from "react-hook-form";
import ApplnDatePicker from "../../lib/components/datepicker/ApplnDatePicker";

function JobCard(props) {
  const [selectedDate, setSelectedDate] = React.useState(null);
  const [fromTime, setFromTime] = React.useState("");
  const [toTime, setToTime] = React.useState("");
  const [render, setRender] = useState(0);
  const [disable, setDisable] = useState(true);
  const allowedFileTypes = ['pdf', 'doc', 'png', 'docx', 'jpg', 'jpeg'];
  const [showToaster1, setShowToaster1] = useState(false);
  const [showToaster2, setShowToaster2] = useState(false);
  const methods = useForm({
    //resolver: yupResolver(schema),
    mode: "onChange",
    reValidateMode: "onChange",
    defaultValues: "",
  });

  useEffect(() => {
    console.log("props in jobCard", props);
   // setDisable(true);
  });

  const validate = (job) => {
    job.touched = true;
    console.log("validate job:: ", job);

    var count = 0;
    
    job.requestContainerList.map((container, inx) => {
      if (
        container.tokenOut &&
        container.tokenOutSlotFrom !== undefined &&
        container.tokenOutSlotTo !== undefined &&
        container.tokenOutDate &&
        container.etokenDto
      ) {
        if (container.valid !== true) {
          container.valid = true;
        }
      } else {
        if (container.valid === true) {
          container.valid = false;
        } else container.valid = false;
      }
    });
    job.requestContainerList.map((container, inx) => {
      if (container.valid === true) count++;
    });
    if (count === job.requestContainerList.length) job.disabled = false;
    else job.disabled = true;
    var cnt = 0;
    var disCnt = 0;
    props.jobs.map((jb, inx) => {
      if (jb.disabled !== undefined && jb.touched===true) {
        cnt = cnt + 1;
        if (jb.disabled === false)
          disCnt = disCnt + 1;
      }
     
    });
    setRender(render + 1);
    if (cnt === disCnt)
      setDisable(false);
    else
    setDisable(true);
  };

  return (
    <>
       <FormProvider {...methods}>
       <Button
                  style={{ textTransform: "none" ,marginLeft:"1120px"}}
                  color="primary"
                  variant="contained"
                  disabled={disable}
                  onClick={() => {
                    console.log("rrrrrr", props.jobs);
                    let e = props.jobs.filter(job => job.disabled === false);
                    console.log("list of jobs for server", e);
                    let obj = {
                      url: `${endpointContants.updateToken}`,
                      body: e,
                    };
                    {
                      postHttp(obj, true)
                        .then((response) => {
                          console.log("received response", response);
                          setDisable(true);
                          props.reRender();
                        })
                        .catch((error) => {
                          // const errMsg = error.message;
                          // dispatch(fetchUserFailure(errMsg));
                        });
                    }
                   }}
                >
                  update
                </Button>
      {props.jobs && props.jobs.length > 0 ? (
        props.jobs.map((job, inx) => (
          <Card
            key={job.referenceNumber}
            style={{ width: "1200px", marginTop: "20px" }}
          >
            <div className="row">
              <Box
                style={{
                  width: "2rem",
                  height: "2rem",
                  color: "white",
                  textAlign: "center",
                  marginTop: "5px",
                  backgroundColor: "#0E1B3D",
                  marginLeft: "40px",
                  border: "2px solid #ccc",
                }}
              >
                {inx + 1}
              </Box>
              <div className="col">
                <InputLabel>Job Number#{job.referenceNumber}</InputLabel>
              </div>
              {/* <div className="col" style={{ marginLeft: "800px" }}>
                <Button
                  style={{ textTransform: "none" }}
                  color="primary"
                  variant="contained"
                  disabled={job.disabled}
                  onClick={() => {
                    console.log("rrrrrr", job);
                    let obj = {
                      url: `${endpointContants.updateToken}`,
                      body: job,
                    };
                    {
                      postHttp(obj, true)
                        .then((response) => {
                          console.log("received response", response);

                          props.reRender();
                        })
                        .catch((error) => {
                          // const errMsg = error.message;
                          // dispatch(fetchUserFailure(errMsg));
                        });
                    }
                  }}
                >
                  update
                </Button>
              </div> */}
              <div className="col-md"  style={{ marginLeft: "900px" }}>
                <IconButton
                  style={{ color: "black" }}
                  onClick={() => {
                    props.onExpandClicked(job);
                    setShowToaster1(false);
                    setShowToaster2(false);
                  }}
                >
                  {" "}
                  {job.open ? <MinimizeIcon /> : <AddIcon />}{" "}
                </IconButton>
              </div>
            </div>
            <Grid item container xs={12} sm spacing={1}>
              <Grid item xs={4} sm={2}>
                <InputLabel
                  style={{
                    fontSize: "13px",
                    color: "black",
                    marginLeft: "20px",
                  }}
                >
                  Consignee Name
                </InputLabel>

                <InputLabel
                  style={{
                    fontSize: "13px",
                    color: "black",
                    marginLeft: "20px",
                  }}
                >
                  {job.consigneeName}
                </InputLabel>
              </Grid>

              {/* <Grid item xs={6} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  Date & Time
                </InputLabel>

                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  {job.dateAndTime}
                </InputLabel>
              </Grid> */}

              <Grid item xs={4} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  Truck Number
                </InputLabel>

                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  {job.vehicleRegNo}
                </InputLabel>
              </Grid>

              {/* <Grid item xs={6} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  Truck Type
                </InputLabel>

                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  {job.vehicleType}
                </InputLabel>
              </Grid> */}

              <Grid item xs={4} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  <img src="./location-pin.svg" /> {job.dropAddress}
                </InputLabel>
              </Grid>
              <Grid item xs={4} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  Requester Company Name
                </InputLabel>

                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  {job.requestedBy}
                </InputLabel>
              </Grid>
              <Grid item xs={4} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  Contact Person 
                </InputLabel>

                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  {job.requesterContactName}
                </InputLabel>
              </Grid>
              <Grid item xs={4} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  Contact Number
                </InputLabel>

                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  {job.requesterContactNumber}
                </InputLabel>
              </Grid>
            </Grid>
            
      
              
            {job.open && (
              <>
                <Table>
                  <TableBody>
                    <TableRow style={{ borderTop: "1px solid grey" }}>
                      <TableCell style={{ borderBottom: "none" }}>
                        <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          Container Number
                        </InputLabel>
                      </TableCell>

                      <TableCell style={{ borderBottom: "none" }}>
                        <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          Declaration#
                        </InputLabel>
                      </TableCell>

                      <TableCell style={{ borderBottom: "none" }}>
                        <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          Booking#
                        </InputLabel>
                      </TableCell>

                      <TableCell style={{ borderBottom: "none" }}>
                        <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          Pickup
                        </InputLabel>
                      </TableCell>

                      <TableCell style={{ borderBottom: "none" }}>
                        <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          Date And Time
                        </InputLabel>
                      </TableCell>


                      <TableCell style={{ borderBottom: "none" }}>
                        <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          Token Number
                        </InputLabel>
                      </TableCell>

                      <TableCell style={{ borderBottom: "none" }}>
                        <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          Token Slot
                        </InputLabel>
                      </TableCell>
                      <TableCell style={{ borderBottom: "none" }}>
                        <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          eToken
                        </InputLabel>
                      </TableCell>
                    </TableRow>

                    {job.requestContainerList.map((container, inx) => (
                      <TableRow key={container.container_number}>
                        <TableCell>
                          <InputLabel
                            style={{ fontSize: "13px", color: "black" }}
                          >
                            {container.container_number}
                          </InputLabel>
                        </TableCell>

                        <TableCell>
                          <InputLabel
                            style={{ fontSize: "13px", color: "black" }}
                          >
                            {container.boeNumber}
                          </InputLabel>
                        </TableCell>

                        <TableCell>
                          <InputLabel
                            style={{ fontSize: "13px", color: "black" }}
                          >
                            {container.requestDetailsNumber}
                          </InputLabel>
                        </TableCell>

                        <TableCell>
                          <InputLabel
                            style={{ fontSize: "13px", color: "black" }}
                          >
                            <img src="./location-pin.svg" />{" "}
                            {container.pickupLocation}
                          </InputLabel>
                        </TableCell>

                        <TableCell>
                         
                          <InputLabel
                            style={{ fontSize: "13px", color: "black" }}
                          >
                            {container.date_time}
                          </InputLabel>
                        </TableCell>

                        <TableCell>
                          <TextField
                            id="outlined-basic"
                            label="Token#"
                            variant="outlined"
                            size="small"
                            defaultValue={container.tokenOut}
                            onChange={(e) => {
                              container.tokenOut = e.target.value;
                              console.log("blurred", container.tokenOut);
                              // updateTokenUnassigned(job.token);
                              validate(job);
                            }}
                          />
                        </TableCell>
                        <TableCell>
                          <Grid container>
                            <Grid item xs={5} style={{marginTop:"20px"}}>
                              {/* <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                <KeyboardDatePicker
                                  InputAdornmentProps={{ position: "start" }}
                                  disableToolbar
                                  variant="inline"
                                  format="dd/MM/yyyy"
                                  margin="normal"
                                  id="date-picker-inline"
                                  value={
                                    container.tokenOutDate
                                      ? container.tokenOutDate
                                      : null
                                  }
                                  onChange={(date) => {
                                    let datestr = '';
                                    console.log("selected date::::::::", date);
                                    //console.log("xxxxxxxxxxxxxxxxx::", moment(date).format('dd/MM/yyyy'));
                                   // container.tokenOutDate = moment(date).format('DD/MM/YYYY');
                                   if (date && date !== 'Invalid Date') {
                                    let day = date.getDate();
                                    let month = date.getMonth() + 1;
                                    let year = date.getFullYear();
                                     datestr = day + '/' + (date.getMonth() + 1) + '/' + year;
                                     console.log("selected date::::::::1", datestr);
                                    setSelectedDate(datestr);
        
                                }
                                    container.tokenOutDate = datestr;
                                    validate(job);
                                  }}
                                  KeyboardButtonProps={{
                                    style: {
                                      fontSize: "12px",
                                      color: "#1FA5FF",
                                      padding: 0,
                                    },
                                  }}
                                />
                              </MuiPickersUtilsProvider> */}
                              <ApplnDatePicker name={"dateTime"} label="" iconColor="#1FA5FF" disablePastDate={true}  onChange={(e) => {
                                container.tokenOutDate = methods.getValues().dateTime;
                                validate(job);
                                console.log("selected date ::::", e);
                          }}/>
                            </Grid>
                            <Grid item xs={1}>
                              <hr
                                style={{
                                  width: "0px",
                                  height: "50px",
                                  backgroundColor: "#D3D3D3",
                                }}
                              ></hr>
                            </Grid>

                            <Grid item xs={2}>
                              <TextField
                                id="toTime"
                                type="time"
                                InputLabelProps={{
                                  shrink: true,
                                }}
                                inputProps={{
                                  size: "small",
                                }}
                                defaultValue={container.tokenOutSlotFrom}
                                onChange={(event) => {
                                  setToTime(event.target.value);
                                  container.tokenOutSlotFrom =
                                    event.target.value;
                                  validate(job);
                                  //  setFromDate(event.target.value);
                                }}
                              />
                              <TextField
                                id="fromTime"
                                type="time"
                                size="small"
                                inputProps={{
                                  size: "small",
                                }}
                                defaultValue={container.tokenOutSlotTo}
                                onChange={(event) => {
                                  setFromTime(event.target.value);
                                  container.tokenOutSlotTo = event.target.value;
                                  validate(job);
                                  //  setFromDate(event.target.value);
                                }}
                              />
                            </Grid>
                          </Grid>
                        </TableCell>
                        <TableCell>
                          <label htmlFor={container.container_number}>
                            <input
                              type="file"
                              name={container.container_number}
                              id={container.container_number}
                              style={{ display: "none" }}
                              onChange={(e) => {
                                
                                const file = e.target.files[0];
                                e.target.value = '';
                                console.log("???????????", container);
                               
                                let promiseData = new Promise(
                                  (resolve, reject) => {
                                    const reader = new FileReader();

                                    reader.onload = (event) => {
                                      resolve(event.target.result);
                                    };

                                    reader.onerror = (err) => {
                                      reject(err);
                                    };

                                    reader.readAsDataURL(file);
                                  }
                                );

                                promiseData.then((result) => {
                                  const contentArr = result.split(",");
                                  const fileType = contentArr[0]
                                    .replace("data:", "")
                                    .replace(";base64", "");
                                  let etokenDto = {};
                                  console.log("file type::", fileType);
                                  console.log("file size::", file.size);
                                  if (allowedFileTypes.some(item => fileType.split("/")[1] === item)) {
                                    if (file.size <= 500000) {
                                      etokenDto.fileContent = contentArr[1];
                                      etokenDto.fileType = fileType;
                                      etokenDto.tokenType = "FCL_OUT";
                                      etokenDto.fileName = file.name;
                                      container.etokenDto = etokenDto;
                                      console.log("container::", container);
                                      console.log("etoken::", container.etokenDto);
                                      container.upload = true;
                                      validate(job);
                                      setShowToaster1(false);
                                      setShowToaster2(false);
                                    } else {
                                      console.log("file size error");
                                      container.upload = false;
                                      
                                      validate(job);
                                      setShowToaster1(true);
                                    }
                                  } else {
                                    console.log("file format error");
                                    container.upload = false;
                                    
                                    validate(job);
                                    setShowToaster2(true);
                                  }
                                }
                                );
                              }}
                              //  onChange={() => { selectFile(Event, job,props.key) }}
                            />

                            {!container.upload && (
                              <img src="./token_upload.svg" />
                            )}
                            {container.upload && (
                              <img src="./token_success.svg" />
                            )}
                              {showToaster1 && (
                                        <Toast
                                          icon="error"
                                          title="File size error"
                                          message="Allowed maximum size is 500 KB"
                                          showToast={() => {
                                            setShowToaster1(false);
                                          }}
                                          position="top-right"
                                        />
                            )}
                             {showToaster2 && (
                                        <Toast
                                          icon="error"
                                          title="File format not supported"
                                          message="*Only files with the following extensions are allowed *.pdf,.doc,.png,.jpg,.jpeg  "
                                          showToast={() => {
                                            setShowToaster2(false);
                                          }}
                                          position="top-right"
                                        />
                                      )}
                          </label>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </>
            )}
          </Card>
        ))
      ) : (
        <Paper
          elevation={5}
          style={{
            borderRadius: 8,
            padding: "30px",
            marginTop: 20,
            minWidth: "760px",
            minHeight: "100px",
            color: "#FF7171",
          }}
        >
          <Grid container direction="row" spacing={5}>
            <Grid item sm={12} xs={12}>
              <Typography
                variant="subtitle1"
                style={{ fontSize: "16px", textAlign: "center" }}
              >
                <b>{"No records found"}</b>
              </Typography>
            </Grid>
          </Grid>
        </Paper>
          )}
        </FormProvider>
    </>
  );
}

export default React.memo(JobCard);
