import React, { useState, useEffect } from "react";
import { ElementInputProps } from "../../common/ElementInputProps";
import DateFnsUtils from "@date-io/date-fns";
import { FieldAttributes, Field, FieldProps, useField } from "formik";
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import { Controller, useFormContext } from "react-hook-form";

type MydateProps = {} & ElementInputProps & FieldAttributes<{}>;

const ApplnDatePicker: React.FC<MydateProps> = (props) => {
  const methods = useFormContext();
  const { errors, getValues, setValue, register, control } = methods;
  const [compDate, setCompdate] = useState(null);
 

  useEffect(() => {
    var str: any = control.defaultValuesRef.current[props.name];
    console.log("date " + str);
    if (str && str.length > 0) {
      let day = str.split("/")[0];
      let mon = str.split("/")[1];
      let yr = str.split("/")[2];
      let myDate = new Date(yr, mon - 1, day);
      if (myDate instanceof Date) setCompdate(myDate);
    }
  }, []);

  const handleChange = (val: any) => {
    console.log(val);
    if (val && val !== "Invalid Date") {
      let day = val.getDate();
      let month = val.getMonth() + 1;
      let year = val.getFullYear();
      let datestr = day + "/" + (val.getMonth() + 1) + "/" + year;
      setValue(props.name, datestr);
      methods.trigger(props.name);
      props.onChange(val);
    }
  };
  return (
    <>
  
      <Controller
        as={
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker
            //  fullWidth
              label={props.label}
              placeholder={props.label}
              value={compDate}
              variant="inline"
              format="dd/MM/yyyy"
              inputVariant="outlined"
              size="small"
              autoOk={true}
              error={errors[props.name] ? true : false}
              helperText={errors[props.name]?.message}
              onChange={(val) => {
                console.log(val);
                setCompdate(val);
                handleChange(val);
              }}
              disablePast={props.disablePastDate?props.disablePastDate:false}
              InputProps = {{
                endAdornment: null,
                style:{
                    fontSize: '16px',
                    fontFamily:'Dubai Regular'
                   }
            }}
            InputLabelProps={{
               style:{
                fontSize: '16px',
                fontStyle:"oblique",
                fontFamily:'Dubai Regular'
               }}}
            KeyboardButtonProps={{
                style:{
                    fontSize: '16px',
                    color:props.iconColor,
                    padding:0
                   }   
            }}
            />
          </MuiPickersUtilsProvider>
        }
        name={props.name}
        control={control}
      />
    </>
  );
};

export default React.memo(ApplnDatePicker);
