import React, { useState } from "react";
import { Card } from "@material-ui/core/";
import {
  Table,
  TableBody,
  TableCell,
  TableRow,
  IconButton,
  Grid,
  TextField,
  Button,
} from "@material-ui/core";
import Box from "@material-ui/core/Box";
import { InputLabel } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import MinimizeIcon from "@material-ui/icons/Minimize";
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import { FormProvider, useForm } from "react-hook-form";
import ApplnDatePicker from "../../lib/components/datepicker/ApplnDatePicker";
import Toast from "../../lib/components/toast/ErrorToast";

function EmptyIn(props) {
  const job = props.job;
  const [reRender, setReRender] = useState(0);
  const [selectedDate, setSelectedDate] = React.useState(null);
  const allowedFileTypes = ['pdf', 'doc', 'png', 'docx', 'jpg', 'jpeg'];
  const [showToaster1, setShowToaster1] = useState(false);
  const [showToaster2, setShowToaster2] = useState(false);
  
  const methods = useForm({
    //resolver: yupResolver(schema),
    mode: "onChange",
    reValidateMode: "onChange",
    defaultValues: "",
  });

  const updateJob = (containerNumber) => {
    var container = job.requestContainerList.find((x) => {
      return x.container_number === containerNumber;
    });

    if (
      container.tokenInDate &&
      container.tokenIn &&
      container.tokenInSlotFrom &&
      container.tokenInSlotTo &&
      container.etokenDto
    ) {
      if (!container.valid) {
        container.valid = true;

        props.updateJobList(job.referenceNumber,container);
      }
    } else {
      if (container.valid === true) {
        container.valid = false;

        props.updateJobList(job.referenceNumber,container);
      }
    }
  };

  return (
    <>
       <FormProvider {...methods}>
      <Card
        key={job.referenceNumber}
        style={{ width: "1200px", marginTop: "20px" }}
      >
        <div className="row">
          <Box
            style={{
              width: "2rem",
              height: "2rem",
              color: "white",
              textAlign: "center",
              marginTop: "5px",
              backgroundColor: "#0E1B3D",
              marginLeft: "40px",
              border: "2px solid #ccc",
            }}
          >
            {props.index + 1}
          </Box>
          <div className="col-md">
            <InputLabel>Job Number#{job.referenceNumber}</InputLabel>
          </div>

          <div className="col-md">
            <IconButton
              style={{ color: "black", marginLeft: "450px" }}
              onClick={() => {
                job.open = !job.open;
                setReRender(reRender + 1);
                setShowToaster1(false);
                setShowToaster2(false);
              }}
            >
              {" "}
              {job.open ? <MinimizeIcon /> : <AddIcon />}{" "}
            </IconButton>
          </div>
        </div>
        <Grid item container xs={12} sm spacing={1}>
          <Grid item xs={4} sm={2}>
            <InputLabel style={{ fontSize: "13px", color: "black" }}>
              Consignee Name
            </InputLabel>

            <InputLabel
              style={{ fontSize: "13px", color: "black", marginTop: "2px" }}
            >
              {job.contactDetails}
            </InputLabel>
          </Grid>

          <Grid item xs={4} sm={2}>
            <InputLabel style={{ fontSize: "13px", color: "black" }}>
              Total Containers
            </InputLabel>

            <InputLabel
              style={{ fontSize: "13px", color: "black", marginTop: "2px" }}
            >
              {job.noOfContainers}
            </InputLabel>
          </Grid>

          <Grid item xs={4} sm={2}>
            <InputLabel style={{ fontSize: "13px", color: "black" }}>
              FCL OUT Truck
            </InputLabel>

            <InputLabel
              style={{ fontSize: "13px", color: "black", marginTop: "2px" }}
            >
              {job.vehicleRegNo}
            </InputLabel>
          </Grid>

          {/* <Grid item xs={6} sm={2}>
            <InputLabel style={{ fontSize: "13px", color: "black" }}>
              FCL OUT Truck Type
            </InputLabel>

            <InputLabel
              style={{ fontSize: "13px", color: "black", marginTop: "2px" }}
            >
              {job.vehicleType}
            </InputLabel>
          </Grid> */}

          <Grid item xs={4} sm={2}>
            <InputLabel style={{ fontSize: "13px", color: "black" }}>
              <img src="./location-pin.svg" /> {job.dropAddress}
            </InputLabel>
            </Grid>
            <Grid item xs={4} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  Requester Company Name
                </InputLabel>

                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  {job.requestedBy}
                </InputLabel>
              </Grid>
              <Grid item xs={4} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  Contact Person 
                </InputLabel>

                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  {job.requesterContactName}
                </InputLabel>
              </Grid>
              <Grid item xs={4} sm={2}>
                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  Contact Number
                </InputLabel>

                <InputLabel style={{ fontSize: "13px", color: "black" }}>
                  {job.requesterContactNumber}
                </InputLabel>
              </Grid>
        </Grid>
        {job.open && (
          <>
            <hr style={{ backgroundColor: "#D3D3D3" }}></hr>
            <Table size="small">
              <TableBody>
                <TableRow>
                  <TableCell style={{ borderBottom: "none" }}>
                    <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                      Container Number
                    </InputLabel>
                  </TableCell>

                  <TableCell style={{ borderBottom: "none" }}>
                    <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                      Declaration#
                    </InputLabel>
                  </TableCell>

                  <TableCell style={{ borderBottom: "none" }}>
                    <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                      Booking#
                    </InputLabel>
                  </TableCell>

                  <TableCell style={{ borderBottom: "none" }}>
                    <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                      MT IN Truck
                    </InputLabel>
                    </TableCell>

                    <TableCell style={{ borderBottom: "none" }}>
                    <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                   Date and Time
                    </InputLabel>
                  </TableCell>

                  <TableCell style={{ borderBottom: "none" }}>
                    <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                      Token Number
                    </InputLabel>
                  </TableCell>

                    <TableCell style={{ borderBottom: "none" }}>
                      <Grid container spacing={2}>
                      <Grid item xs={6} style={{marginTop:"20px"}}>
                    <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          Token Time Slot
                    </InputLabel>
                        </Grid>
                        </Grid>
                  </TableCell>
                    <TableCell style={{ borderBottom: "none" }}>
                    <Grid
                          item
                          style={{
                            position: "fixed",
                            marginLeft: "80px",
                          //  marginTop: "20px",
                          }}
                        >
                    <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                          eToken
                    </InputLabel>
                    </Grid>
                  </TableCell>
                </TableRow>

                {job.requestContainerList.map((container) => (
                  <TableRow key={container.container_number}>
                    <TableCell>
                      <InputLabel style={{ fontSize: "13px", color: "black" }}>
                        {container.container_number}
                      </InputLabel>
                    </TableCell>

                    <TableCell>
                      <InputLabel style={{ fontSize: "13px", color: "black" }}>
                        {container.boeNumber}
                      </InputLabel>
                    </TableCell>

                    <TableCell>
                      <InputLabel style={{ fontSize: "13px", color: "black" }}>
                        {container.requestDetailsNumber}
                      </InputLabel>
                    </TableCell>

                    <TableCell>
                      <InputLabel style={{ fontSize: "13px", color: "black" }}>
                        {container.mtTruck}
                      </InputLabel>
                    </TableCell>

                    <TableCell>
                      <InputLabel style={{ fontSize: "13px", color: "black" }}>
                        {container.requestTimeInSlot}
                      </InputLabel>
                    </TableCell>

                    <TableCell>
                      <TextField
                        id="outlined-basic"
                        label="Token#"
                        variant="outlined"
                        size="small"
                        onChange={(e) => {
                          container.tokenIn = e.target.value;
                          console.log("blurred", container.tokenIn);
                          updateJob(container.container_number);
                        }}
                      />
                    </TableCell>
                    <TableCell colSpan="4">
                      <Grid container spacing={2}>
                        <Grid item xs={6} style={{marginTop:"20px"}}>
                          {/* <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <KeyboardDatePicker
                              InputAdornmentProps={{ position: "start" }}
                              disableToolbar
                              variant="inline"
                              format="dd/MM/yyyy"
                              margin="normal"
                              value={container.tokenInDate?container.tokenInDate:null}
                              onChange={(date) => {
                                setSelectedDate(date);
                                container.tokenInDate = date;
                                updateJob(container.container_number);
                              }}
                              KeyboardButtonProps={{
                                style: {
                                  fontSize: "12px",
                                  color: "#1FA5FF",
                                  padding: 0,
                                },
                              }}
                            />
                          </MuiPickersUtilsProvider> */}
                           <ApplnDatePicker name={"dateTime"} label="" iconColor="#1FA5FF" disablePastDate={true}   onChange={(e) => {
                            container.tokenInDate = methods.getValues().dateTime;
                            setSelectedDate(methods.getValues().dateTime);
                            updateJob(container.container_number);
                                console.log("selected date ::::", e);
                          }}/>
                        </Grid>
                        <Grid item xs={1}>
                          <hr
                            style={{
                              width: "0px",
                              height: "50px",
                              backgroundColor: "#D3D3D3",
                            }}
                          ></hr>
                        </Grid>

                        <Grid item xs={2}>
                          <TextField
                            id="toTime"
                            type="time"
                            InputLabelProps={{
                              shrink: true,
                            }}
                            inputProps={{
                              size: "small",
                            }}
                            onChange={(event) => {
                              container.tokenInSlotFrom = event.target.value;
                              updateJob(container.container_number);
                            }}
                          />
                          <TextField
                            id="fromTime"
                            type="time"
                            size="small"
                            inputProps={{
                              size: "small",
                            }}
                            onChange={(event) => {
                              container.tokenInSlotTo = event.target.value;
                              updateJob(container.container_number);
                            }}
                          />
                        </Grid>

                      </Grid>
                    </TableCell>
                    <TableCell>
                      
                    <Grid
                          item
                          style={{
                            position: "relative",
                            marginLeft: "25px",
                            marginTop: "5px",
                          }}
                        >
                          <label htmlFor={container.container_number}>
                            <input
                              type="file"
                              name={container.container_number}
                              id={container.container_number}
                              style={{ display: "none" }}
                              onChange={(e) => {
                                const file = e.target.files[0];
                                e.target.value = '';
                                let promiseData = new Promise(
                                  (resolve, reject) => {
                                    const reader = new FileReader();

                                    reader.onload = (event) => {
                                      resolve(event.target.result);
                                    };

                                    reader.onerror = (err) => {
                                      reject(err);
                                    };

                                    reader.readAsDataURL(file);
                                  }
                                );

                                promiseData.then((result) => {
                                  const contentArr = result.split(",");
                                  const fileType = contentArr[0]
                                    .replace("data:", "")
                                    .replace(";base64", "");
                                  let etokenDto = {};
                                  container.etokenDto = "";
                                  if (allowedFileTypes.some(item => fileType.split("/")[1] === item)) {
                                    if (file.size <= 500000) {
                                      etokenDto.fileContent = contentArr[1];
                                      etokenDto.fileType = fileType;
                                      etokenDto.tokenType = "EMPTY_IN";
                                      etokenDto.fileName = file.name;
                                      container.etokenDto = etokenDto;
                                      console.log("container::", container);
                                      console.log("etoken::", container.etokenDto);
                                      setReRender(reRender + 1);
                                      updateJob(container.container_number);
                                      setShowToaster1(false);
                                      setShowToaster2(false);
                                    } else {
                                      setShowToaster1(true);
                                      setReRender(reRender + 1);
                                    }
                                  } else {
                                    setShowToaster2(true);
                                    setReRender(reRender + 1);
                                  }
                                }
                                );
                              }}
                              //  onChange={() => { selectFile(Event, job,props.key) }}
                            />
                            {container.etokenDto && (
                              <img src="./token_success.svg" />
                            )}

                            {!container.etokenDto && (
                              <img src="./token_upload.svg" />
                            )}
                              {showToaster1 && (
                                        <Toast
                                          icon="error"
                                          title="File size error"
                                          message="Allowed maximum size is 500 KB"
                                          showToast={() => {
                                            setShowToaster1(false);
                                          }}
                                          position="top-right"
                                        />
                            )}
                             {showToaster2 && (
                                        <Toast
                                          icon="error"
                                          title="File format not supported"
                                          message="*Only files with the following extensions are allowed *.pdf,.doc,.png,.jpg,.jpeg  "
                                          showToast={() => {
                                            setShowToaster2(false);
                                          }}
                                          position="top-right"
                                        />
                                      )}
                          </label>
                        </Grid>
                      </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </>
        )}
        </Card>
        </FormProvider>
    </>
  );
}
export default React.memo(EmptyIn);
